$other-color:#e1e5cc;
$main-color:#939393;

$font-family-reset:'Segoe UI',Roboto,Arial,sans-serif;
$font-family-heading:'Helvectica', Arial, Roboto,sans-serif;

.breadcrumb-nav{
grid-row:2;
max-width:100%;
font-size:10px;
padding: 0 2%;
@include text-fluid($font-family-reset, 400, $main-color, 361px, 2880px, 14px, 28px);
}

.first-section .breadcrumb-nav  ol li{
}

.breadcrumb-nav  ol {
list-style-type:none;
display:flex;
flex-wrap:wrap;
align-items:center;
padding:0;
}

.breadcrumb-nav  a {
text-align:left;
font-size:0.9em;
color: #49ca5e;
text-decoration:underline !important;
}

.breadcrumb-nav  a:hover {
font-size:120%;
color:#96e0a2;
}


.breadcrumb-nav  span {
text-align:left;
padding:0.2em 0.5em 0em 0.5em;
font-size:0.65em;
color: #49ca5e;
}

ul[class="newsletter-admin-list"]{
padding: 1em 0em 0em 3em;
list-style:disc !important;
}

ul[class="newsletter-admin-list"] > li{
padding: 0.5em 0em;
}


@include responsive(phone-500){
ul[class="newsletter-admin-list"]{
padding: 1em 0em 0em 2em;
}
}//end max 500px or 31.25em


.newsletter-admin-links{
color:#49ca5e;
}

.newsletter-admin-links.visited{
color:#49ca5e;
}

.newsletter-admin-links:hover{
color:#96e0a2;
font-size:1.2em;
}

.newsletter-admin-links:active{
text-decoration:none;
}

.navigation > .navigation_container >.main-nav >ul > li > .nav-link{
color:#fffff5 !important;
}

main{
padding: 0 2%;
grid-column: 1;
grid-row: 3;
min-height: 100vh;
}

body > .main > .main-content a {color:#49ca5e;}

body > .main > .main-content a.visited {color:#49ca5e;}
body > .main > .main-content a.active {color:#49ca5e;}

body > .main > .main-content a:hover {color:#96e0a2;size:120%;}

.main-content {

@include responsive(small-phone){
@include text-fluid($font-family-reset, 400, $main-color, 575px, 768px, 22px, 28px);
width:100%;
}//500px x 1.15 = 574px


@include responsive(pixel-2){
@include text-fluid($font-family-reset, 400, $main-color, 280px, 575px, 18px, 22px);
}//376px x 1.09 = 411px


@include responsive(phone){
@include text-fluid($font-family-reset, 400, $main-color, 768px, 992px, 22px, 27px);
width:100%;
margin:0 auto;
}//500px x 1.15 = 574px


@include responsive(big-tablet){
@include text-fluid($font-family-reset, 400, $main-color, 992px, 1200px, 18px, 30px);
}//830px x 1.19 = 992px

@include responsive(big-desktop){
@include text-fluid($font-family-reset, 400, $main-color, 1200px, 1920px, 18px, 30px)
width:90%;
}//1200px x 1.16 = 1400px


@include responsive(developer-standard){
@include text-fluid($font-family-reset, 400, $main-color, 1920px, 2250px, 19px, 35px);
width:80%;
}//1920px x 1.0666 = 1920px

@include responsive(big-tv){
@include text-fluid($font-family-reset, 400, $main-color, 2250px, 2880px, 20px, 38px);
}//2250px x 1.111 2500px


}//end .main-content

.main-content p b{
color:#939a9e;
}

.main-content .img-multi-left{
display:flex;
flex-wrap:wrap;
align-items:flex-start;
justify-content:flex-start;
}

.main-content .img-multi-right{
display:flex;
flex-wrap:wrap;
align-items:flex-start;
justify-content:flex-end;
}

.main-content .img-multi-center{
display:flex;
flex-wrap:wrap;
align-items:flex-start;
justify-content:center;
}

.main-content .img-multi-even{
display:flex;
flex-wrap:wrap;
align-items:flex-start;
justify-content:space-evenly;
}

